import Text from "components/Text/Text";
import React from "react";

interface IProps {
  label: string;
  value?: string | undefined;
  titleClass?: string | undefined;
}

const TextData = (props: IProps) => {
  const { label, value, titleClass } = props;
  return (
    <div>
      <Text
        label={label}
        className={
          titleClass ? titleClass : "  text-primary-labelColor  text-xs  text-left   mb-1  "
        }
      />
      <p className="text-primary-labelColor  text-xs  text-left   mb-1  ">{value}</p>
    </div>
  );
};

export default TextData;
